table {
  display: grid;
  border-collapse: collapse;
  min-width: 100%;
  grid-template-columns:
    minmax(38px, 0.2fr)
    minmax(100px, 1.37fr)
    minmax(100px, 1.37fr)
    minmax(100px, 1.37fr)
    minmax(100px, 1.4fr)
    minmax(100px, 1.37fr)
    minmax(100px, 1.3fr)
    minmax(100px, 1.37fr);
  font-size: 15px;
}

thead,
tbody,
tr {
  display: contents;
}

th,
td {
  padding: 10px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

th {
  position: sticky;
  top: 0;
  background: #6c7ae0;
  text-align: left;
  font-weight: normal;
  font-size: 1.1rem;
  color: white;
}

th:last-child {
  border: 0;
}

td {
  padding-top: 10px;
  padding-bottom: 10px;
  color: #808080;
}

tr:nth-child(even) td {
  background: #f8f6ff;
}
